import DocumentTitle from 'react-document-title';
import { Route } from 'react-router-dom';
import { routeConfig } from './config';
import AllPages from '../views';
import AllLayouts from '../layouts';
const AllCompoents = {
    ...AllPages,
    ...AllLayouts
};


